<template>
  <div>
    <v-container class="aranceles__container">
      <v-row>
        <v-col>
          <v-row class="aranceles_row_sucursal flex-column-reverse flex-md-row">
            <v-col class="aranceles_row_sucursal__select col-12 col-md-4">
              <div class="ars_select__main">
                <v-select
                  v-model="sucursal"
                  :items="sucursales"
                  item-text="Nombre"
                  item-value="CodSucursal"
                  label="Sucursal"
                  filled
                  dense
                  hide-details
                  background-color="warning"
                  color="warning"
                  item-color="warning"
                  dark
                />
              </div>
            </v-col>
            <v-col class="aranceles_row_sucursal__title col-12 col-md-8">
              <div class="ars_title__main">
                <slide-x-right-transition>
                  <h1 v-if="titleStatus">
                    {{ setCompleteTitle }}
                  </h1>
                </slide-x-right-transition>
              </div>
            </v-col>
          </v-row>
          <v-row class="aranceles_row_up">
            <v-col class="aranceles_row_up__left col-12 col-md-5">
              <div class="aru_left_1">
                <span>ARANCELES PARA AFILIADOS A FONASA</span>
              </div>
              <div class="aru_left_2">
                <span>Aranceles al alcance de la mayoría. Para los pacientes
                  afiliados a Fonasa, tenemos valores especiales, como por
                  ejemplo,
                  <b>la sesión de psicología a ${{
                    arancelFonasa.ValorPrimera
                  }}</b>.</span>
              </div>
              <div class="aru_left_3">
                <v-simple-table
                  dense
                  class="tabla_aranceles"
                >
                  <template v-slot:default>
                    <thead>
                      <tr>
                        <th class="text-left">
                          ESPECIALIDAD
                        </th>
                        <th class="text-left">
                          GRUPO ETARIO
                        </th>
                        <th class="text-left">
                          ARANCEL
                        </th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr
                        v-for="(arancel, a) in aranceles"
                        :key="a"
                        @click="
                          $router.push(`/especialidad/${arancel.CodSiat}`)
                        "
                      >
                        <td
                          class="td_especialidad"
                          :style="arancel.TdBorderTop"
                        >
                          {{ arancel.NombreEspecialidad }}
                        </td>
                        <td :style="arancel.TdBorderTop">
                          {{ arancel.NombreRangoEtario }}
                        </td>
                        <td :style="arancel.TdBorderTop">
                          ${{ formatNumberBySeparator(arancel.ValorPrimera, '.') }}
                        </td>
                      </tr>
                    </tbody>
                  </template>
                </v-simple-table>
              </div>
            </v-col>
            <v-col class="aranceles_row_up__right col-lg-7">
              <div class="aru_right_1">
                <span><strong>PAT:</strong> VALORES CON DESCUENTO</span>
              </div>
              <div class="aru_right_2">
                <span>El Programa de Atención Terapéutica (PAT) es una modalidad de
                  pago en donde se puede adquirir varias sesiones de una
                  determinada especialidad a un valor más económico:</span>
              </div>
              <div class="aru_right_3">
                <v-simple-table
                  dense
                  class="tabla_packs"
                >
                  <template v-slot:default>
                    <thead>
                      <tr>
                        <th class="text-left">
                          ESPECIALIDAD
                        </th>
                        <th class="text-left">
                          SESIONES
                        </th>
                        <th class="text-left">
                          <span
                            v-html="
                              $vuetify.breakpoint.xs
                                ? '% DESC*'
                                : '% DESCUENTO*'
                            "
                          />
                        </th>
                        <th
                          class="text-left"
                          colspan="2"
                        >
                          ARANCEL
                        </th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr
                        v-for="(pack, p) in packs"
                        :key="p"
                        @click="$router.push(`/especialidad/${pack.CodSiat}`)"
                      >
                        <td
                          class="td_especialidad"
                          :style="pack.TdBorderTop"
                        >
                          {{ pack.NombreEspecialidad }}
                        </td>
                        <td :style="pack.TdBorderTop">
                          {{ pack.CantidadSesiones }} Sesiones
                        </td>
                        <td :style="pack.TdBorderTop">
                          {{ pack.PorcentajeDescuento }}%
                        </td>
                        <td
                          v-if="!$vuetify.breakpoint.xs"
                          style="width: 70px"
                          :style="pack.TdBorderTop"
                        >
                          <strike>
                            ${{ pack.PrecioInicial.toLocaleString('es-CL') }}
                          </strike>
                        </td>
                        <td :style="pack.TdBorderTop">
                          <strike v-if="$vuetify.breakpoint.xs">
                            ${{ pack.PrecioInicial.toLocaleString('es-CL') }}
                          </strike>
                          ${{ pack.PrecioFinal.toLocaleString('es-CL') }}
                        </td>
                      </tr>
                    </tbody>
                  </template>
                </v-simple-table>
                <span>
                  * El descuento se aplica sobre el Arancel Especial Fonasa
                  respectivo.
                </span>
              </div>
            </v-col>
          </v-row>
          <v-row class="aranceles_row_bottom">
            <v-col class="aranceles_row_bottom__left col-lg-5">
              <div class="arb_left_1">
                <span>ARANCELES POR ISAPRES</span>
              </div>
              <div class="arb_left_2">
                <v-img
                  class="img-tarjetas"
                  max-height="229"
                  max-width="516"
                  :src="
                    `https://res.cloudinary.com/dkod5pdlc/image/upload/f_auto/terapiacl/general/convenio-isapre-psicologia`
                  "
                />
              </div>
              <div class="arb_left_3 text-justify">
                <p>
                  <span>
                    Se debe consultar el arancel en la misma institución, ya que
                    son ellos quienes disponen de esta información de acuerdo al
                    plan de salud del afiliado
                  </span>
                </p>
                <p>
                  <span>
                    Las isapres en convenio son: Banmédica, Colmena, Consalud,
                    Cruz Blanca, Nueva MasVida y Vida Tres, y más información
                    puede encontrar
                    <router-link
                      to="/convenios-isapres-atencion-psicologica"
                    >aquí</router-link>
                  </span>
                </p>
              </div>
            </v-col>
            <v-col class="aranceles_row_bottom__right col-lg-7">
              <div class="arb_right_1">
                <div class="arb_right_1__title">
                  <span>ARANCELES TERAPIA ONLINE</span>
                </div>
                <div class="arb_right_1__box">
                  <div class="arb_right_1__box_1">
                    <span>Para información sobre nuestros aranceles de Terapia
                      Online, se debe hacer click
                      <a
                        href="https://terapiaonline.cl/online/home/aranceles"
                      >aquí</a></span>
                  </div>
                  <div class="arb_right_1__box_2">
                    <v-img
                      class="img-tarjetas"
                      max-height="150"
                      max-width="150"
                      :src="
                        `https://res.cloudinary.com/dkod5pdlc/image/upload/f_auto/terapiacl/general/aranceles-psicologia-online`
                      "
                    />
                  </div>
                </div>
              </div>
              <div class="arb_right_2">
                <div class="arb_right_2_title">
                  <span>FORMAS DE PAGO</span>
                </div>
                <div class="arb_right_2__box">
                  <div class="arb_right_2__box_1">
                    <span>
                      Las formas de pago disponibles son las siguientes:
                    </span>
                    <div>
                      <ul>
                        <li>
                          <v-icon small>
                            mdi-check
                          </v-icon>
                          Efectivo
                        </li>
                        <li>
                          <v-icon small>
                            mdi-check
                          </v-icon>
                          Transferencia Bancaria
                        </li>
                        <li>
                          <v-icon small>
                            mdi-check
                          </v-icon>
                          Tarjeta de Dédito
                        </li>
                        <li>
                          <v-icon small>
                            mdi-check
                          </v-icon>
                          Tarjeta de Crédito
                        </li>
                      </ul>
                    </div>
                  </div>
                  <div class="arb_right_2__box_2">
                    <v-img
                      class="img-tarjetas"
                      max-width="300"
                      :src="
                        `https://res.cloudinary.com/dkod5pdlc/image/upload/f_auto/terapiacl/general/aranceles-psicologo`
                      "
                    />
                  </div>
                </div>
              </div>
            </v-col>
          </v-row>
          <v-row />
        </v-col>
      </v-row>
    </v-container>
  </div>
</template>
<script>
  import Vue from 'vue'
  import { mapState } from 'vuex'
  import { SlideXRightTransition } from 'vue2-transitions'
  import formatNumberBySeparator from '@/mixins'
  export default {
    name: 'Aranceles',
    components: {
      SlideXRightTransition,
    },
    mixins: [formatNumberBySeparator],
    data () {
      return {
        sucursal: 1,
        title: 'Aranceles',
        aranceles: '',
        packs: '',
        titleStatus: true,
        arancelFonasa: '',
      }
    },
    computed: {
      ...mapState('web', ['sucursales']),
      setCompleteTitle () {
        let str = ''
        switch (this.sucursal) {
          case 1:
            str = `${this.title} Sucursal Providencia`
            break
          case 2:
            str = `${this.title} Sucursal Viña del Mar`
            break
          case 3:
            str = `${this.title} Sucursal Santiago Centro`
            break
          default:
            str = this.title
            break
        }
        this.getAranceles(this.sucursal)
        this.getPacks(this.sucursal)
        return str
      },
    },
    methods: {
      getAranceles (sucursal) {
        this.titleStatus = false
        Vue.axios
          .get(`/api/aranceles/${sucursal}`)
          .then(response => {
            this.sortItems(response.data.result, 1)
            this.titleStatus = true
          })
          .catch(error => {
            console.log(error)
          })
      },
      getPacks (sucursal) {
        Vue.axios
          .get(`/api/packs/${sucursal}`)
          .then(response => {
            this.sortItems(response.data.result, 2)
          })
          .catch(error => {
            console.log(error)
          })
      },
      sortItems (items, section) {
        const itemsSorted = []

        for (let a = 0; a < items.length; a++) {
          const item = items[a]
          item.TdBorderTop = ''

          if (
            items.slice(0, a).find(element => element.CodSiat === item.CodSiat)
          ) {
            item.NombreEspecialidad = ''
          } else {
            item.TdBorderTop = 'border-top: 1px solid #a7d9c3 !important'
          }

          itemsSorted.push(item)
        }

        if (section === 1) {
          this.aranceles = itemsSorted
          this.arancelFonasa = items.find(i => i.CodSiat === 8)
        } else {
          this.packs = itemsSorted
        }
      },
    },
    metaInfo: {
      title: 'Aranceles',
      titleTemplate: '%s - Centro de Terapia del Comportamiento',
      htmlAttrs: {
        lang: 'es',
        amp: true,
      },
    },
  }
</script>
